import React from 'react';
import ReactDOM from 'react-dom'
import './App.css';
import { useState } from 'react';
import { Button } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMinus, faPlus } from '@fortawesome/free-solid-svg-icons';

const App=()=>{


  let [score, setScore]= useState(0);

  return (
    <div className="container center">
      <h1 className="heading">This is my Counter App</h1>
      <div className="score">
      <h2 className="text-danger pt-5">Score is: <span>{score}</span></h2>
      <div className="heading">
        <Button className="btn btn-success mx-2 p-3" onClick={()=>{setScore(score + 1)}} ><FontAwesomeIcon icon={faPlus} /></Button>
        <Button className="btn btn-danger p-3" onClick={()=>(score>0)? setScore(score - 1): ""}><FontAwesomeIcon icon={faMinus} /></Button>
      </div>
      </div>
    
    </div>
  );
}

export default App;
